body {
  background-color: #2a2d33;
}

.container {
  background-color: #2a2d33;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  color: white;
  width: 100vw;
  height: 100vh;
  display: grid;
  grid-template:
    'logo nav-menu nav-menu' 5em
    'cont-left cont-mid cont-right' 1fr
    'footer footer footer' 5em /
    0.25fr 1fr 0.25fr;
  z-index: -10;
}

.top {
  background-color: #161a20b2;
  border-bottom: 5px solid #161a20;
  backdrop-filter: blur(5px);
}

.logo {
  grid-area: logo;
  text-align: center;
  color: #eeeeee;
}

.logo h1 {
  transform: translateY(-15%);
}

.nav-menu {
  grid-area: nav-menu;
  display: flex;
  z-index: 15;
}

.nav-menu-el {
  width: 7em;
  text-align: center;
  display: flex;
  flex-direction: column;
}

.menu-dropdown {
  font-family: 'Roboto';
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 0em 0em 0.5em 0.5em;
}

.menu-dropdown:hover {
  background-color: #696f79;
  box-shadow: 0px 0.5em 15px rgba(0, 0, 0, 0.5);
}

.menu-dropdown h2:hover {
  box-shadow: none;
  cursor: default;
}

.dropdown-item {
  margin: 5% 0% 5% 0%;
  font-size: 1.1em;
  text-decoration: none;
  width: 100%;
}

.dropdown-item.MuiButtonBase-root:hover {
  background-color: #36393f;
  color: white;
  border-radius: 0;
}

.cont-left {
  grid-area: cont-left;
}

.cont-mid {
  grid-area: cont-mid;
}

.home-img {
  height: 35em;
  width: 100%;
  position: relative;
  overflow: hidden;
}

.home-img .home-img__img img {
  object-fit: fill;
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 0;
  transform: scale(1.5, 1);
}

.home-img .home-img__gradient {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 1;
  background-image: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0) 0%,
    #2a2d3338 80%,
    #2a2d33 100%
  );
}

.home-img .home-img__text {
  color: black;
  position: absolute;
  left: 43%;
  top: 25%;
  z-index: 2;
}

.cont-right {
  grid-area: cont-right;
}

.footer {
  grid-area: footer;
}

.footer-text {
  text-align: center;
  color: #484b52;
}

.metar {
  grid-area: cont-mid;
  height: 100%;
}

.metar-top {
  width: 100%;
}

.metar-container {
  grid-area: cont-mid;
  display: flex;
  flex-direction: row;
}

.metar-box {
  background-color: #444a53;
  border-radius: 1em;
  box-shadow: 0px 0.5em 15px rgba(0, 0, 0, 0.5);
}

.MuiTabs-flexContainer {
  justify-content: center;
}

.MuiButtonBase-root.MuiTab-root {
  color: rgba(0, 0, 0, 0.431);
  flex: 1;
}

.MuiTab-root.Mui-selected {
  background-color: #7c808841;
}

.metar-search {
  max-width: 20em;
  min-width: 20em;
  margin-right: 1em;
}

.metar-search form {
  padding-top: 0.1em;
  padding-bottom: 1em;
  margin-left: 1em;
  margin-right: 1em;
}

.metar-display {
  margin-left: 1em;
  margin-right: 1em;
  width: 75%;
  min-width: none;
}

.metar-display h1 {
  margin-top: 0.1em;
}

.metar-display h3 {
  margin-bottom: 0.5em;
}

.metar-unit h3 {
  margin: 1% 0% 0% 0%;
}

.metar-searchbox ul {
  display: flex;
  flex-direction: row;
  align-content: flex-start;
  padding-left: 0;
}

.metar-display p {
  margin-bottom: 0;
  margin-top: 0.5em;
}

.metar-display__data {
  margin-bottom: 1em;
}

.wind-box {
  display: flex;
  flex-direction: row;
}

.wind-lbox {
  flex-grow: 1;
}

a:link {
  color: inherit;
  font-weight: bold;
}

a:visited {
  color: inherit;
}

a:hover {
  color: #cecece;
}

.test-text-enter {
  opacity: 0;
}

.test-text-enter-active {
  opacity: 1;
  transition: opacity 500ms ease-in;
}

.test-text-exit {
  opacity: 1;
}

.test-text-exit-active {
  opacity: 0;
  transition: opacity 500ms ease-in;
}

.test-text-exit-done {
  opacity: 0;
}

.about-main {
}
